@import url('https://fonts.googleapis.com/css?family=Big+Shoulders+Display&display=swap');
html {
    --main-color: #fff;
    font-size: 62.5%;
    font-family: 'Big Shoulders Display', cursive;
}

body {
    font-size: 2.5rem;
    background-color: #888;
    color: #fff;
}

#nav {
    z-index: 1;
    display: flex;
    position: fixed;
    width: 100%;
    background-color: #ccc;
    -webkit-box-shadow: 2px 5px 16px -8px rgba(0, 0, 0, 1);
    -moz-box-shadow: 2px 5px 16px -8px rgba(0, 0, 0, 1);
    box-shadow: 2px 5px 16px -8px rgba(0, 0, 0, 1);
}

.nav-item, a.nav-item {
    padding: 1.5rem;
    cursor: pointer;
    color: var(--main-color);
    font-size: 3rem;
    display: inline-block;
}

.header {
    color: var(--main-color);
    font-size: 5rem;
    padding: 1.5rem;
}

.my-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    /* max-width: 1160px; */
    /* width: 80vw; */
    /* margin: auto; */
    padding: 10rem 0em 0em 0em;
    text-align: center;
}

.main-text {
    max-width: 500px;
    padding-bottom: 8rem;
}

.main-projects {
    padding-top: 8rem;
    width: 100%;
    background-color: #F7F7F7;
    color: #999;
}

.sub-header {
    font-size: 4rem;
    padding: 1rem;
}